import React, { useState } from 'react';
import { Outlet } from 'react-router-dom';
import {
  AppBar, Toolbar, Typography, Container, Box, Drawer, List, ListItem,
  ListItemText, IconButton, Link as MuiLink
} from '@mui/material';
import { Link } from 'react-router-dom';
import { Menu as MenuIcon } from '@mui/icons-material';

const Layout = () => {
  const [mobileOpen, setMobileOpen] = useState(false);

  const navItems = [
    { label: 'Home', path: '/' },
    { label: 'Create', path: '/create' },
    { label: 'About', path: '/about' },
  ];

  const drawer = (
    <Box sx={{ width: 250, p: 2 }}>
      <Typography variant="h6" sx={{ mb: 2 }}>
        BookmarkletKit
      </Typography>
      <List>
        {navItems.map((item) => (
          <ListItem button component={Link} to={item.path} key={item.label} onClick={() => setMobileOpen(false)}>
            <ListItemText primary={item.label} />
          </ListItem>
        ))}
      </List>
    </Box>
  );

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '100vh' }}>
      <AppBar position="fixed">
        <Toolbar>
          <IconButton
            color="inherit"
            edge="start"
            onClick={() => setMobileOpen(true)}
            sx={{ mr: 2, display: { sm: 'none' } }}
            aria-label="Open navigation menu"
          >
            <MenuIcon />
          </IconButton>
          <Typography
            variant="h6"
            component={Link}
            to="/"
            sx={{ flexGrow: 1, textDecoration: 'none', color: 'white' }}
          >
            BookmarkletKit
          </Typography>
          <Box sx={{ display: { xs: 'none', sm: 'block' } }}>
            {navItems.map((item) => (
              <MuiLink
                component={Link}
                to={item.path}
                color="inherit"
                sx={{ mx: 2, textDecoration: 'none' }}
                key={item.label}
              >
                {item.label}
              </MuiLink>
            ))}
          </Box>
        </Toolbar>
      </AppBar>
      <Drawer
        anchor="left"
        open={mobileOpen}
        onClose={() => setMobileOpen(false)}
        sx={{ display: { xs: 'block', sm: 'none' } }}
      >
        {drawer}
      </Drawer>
      <Container component="main" sx={{ mt: 10, mb: 4, flexGrow: 1 }}>
        <Outlet />
      </Container>
      <Box
        component="footer"
        sx={{ py: 3, px: 2, mt: 'auto', bgcolor: 'grey.200', textAlign: 'center' }}
      >
        <Typography variant="body2" color="text.secondary">
          © {new Date().getFullYear()} <MuiLink href="https://github.com/DamionR/BookmarkletKit">BookmarkletKit</MuiLink> |{' '}
          <MuiLink href="/about">About</MuiLink> |{' '}
          <MuiLink href="https://github.com/DamionR/BookmarkletKit/issues">Support</MuiLink>
        </Typography>
      </Box>
    </Box>
  );
};

export default Layout;